
import './create_account.css'
import 'react-phone-number-input/style.css'

import PhoneInput from "react-phone-number-input";
import { useState } from 'react';

import EndzoneShieldLogo from './images/Endzone_Shield.png'
import EndzoneButton from '../../../components/buttons/endzone-new-button/endzone-button' 

import AddIcon from '@mui/icons-material/Add';
import { Alert } from '@mui/material';
import IconButton from '@mui/material/IconButton';
import CloseIcon from '@mui/icons-material/Close';

function CreateAccount() {
    const [email, setEmail] = useState('');
    const [password1, setPass1] = useState('');
    const [password2, setPass2] = useState('');
    const [first, setFirst] = useState('');
    const [last, setLast] = useState('');
    const [phone, setPhone] = useState('');
    const [team_code, setTeam] = useState('');
    const queryString = window.location.search;
    const urlParams = new URLSearchParams(queryString);
    const joinTeamStatus = urlParams.get('action') === 'joinTeam';
    const createTeamStatus = urlParams.get('action') === 'createTeam';
    const httpHandler = require('react-http-client');
    const [alert, updateAlert] = useState({
        style: {display: "none"},
        severity: "info",
        userMessage: ""
    })

    function closeAlert(){
        updateAlert({
                style: {display: "none"},
                severity: "info",
                userMessage: "" 
            }
        )
    }

    function handleError(request){
        try {   
            var response = JSON.parse(request.message)
            updateAlert({
                style: {display: "block"},
                severity: "error",
                userMessage: response.data
            })
        }
        catch {
            updateAlert({
                style: {display: "block"},
                severity: "error",
                userMessage: "Unexpected Error - Please Contact: endzone.support@gmail.com"
            })
        }
    }

    function sendCreateRequest(e) {
        if(!joinTeamStatus) {
            setTeam("null")
        }
        httpHandler.post(
            "/account/user/create", 
            {
                "first": first,  
                "last": last, 
                "email": email,
                "phone": phone, 
                "password1": password1,
                "password2": password2, 
                "join_action": joinTeamStatus,
                "team_code": team_code
            }
        ).then(
            (request) => {
                if(createTeamStatus)
                {
                    window.location.href = "/account/org";
                }
                else {
                    // If not creating a new team -- auto login
                    window.location.href = "/endzone/hub";
                } 

            }, 
            (request) => {
                handleError(request)
            }
        );
    }

    function handleSubmit(e) {
        document.getElementById("userMessage").innerText = ""
        sendCreateRequest(e)
    }

    function logoDivClick() {
        window.location.href = "/";
    }

    return (
        <div>            
            <div className='alert-div' style = {alert.style}>
                <Alert variant="filled" severity={alert.severity}
                            action={
                                <IconButton
                                aria-label="close"
                                color="inherit"
                                size="small"
                                onClick={() => {
                                    closeAlert();
                                }}
                                >
                                <CloseIcon fontSize="inherit" />
                                </IconButton>
                            }>
                            {alert.userMessage}
                </Alert>
            </div>
            <div className = "create-account-container">
                <div className = 'logoDiv' id = 'logoDiv'>
                        <img src={EndzoneShieldLogo} class = "logo" alt="Endzone Logo" onClick={() => logoDivClick()}></img>
                </div>
                <form className="register-form" onSubmit={handleSubmit}>
                    <label className="input-title" htmlFor="first-name">First Name</label>
                    <input className="input-box" value={first} onChange={(e) => setFirst(e.target.value)}/>
                    
                    <label className="input-title" htmlFor="last-name">Last Name</label>
                    <input className="input-box" value={last} onChange={(e) => setLast(e.target.value)}/>

                    <label className="input-title" htmlFor="email">Email</label>
                    <input className="input-box" value={email} onChange={(e) => setEmail(e.target.value)}/>
                    <label className="input-title" htmlFor="phone">Phone Number</label>
                    <PhoneInput 
                        defaultCountry="US" 
                        value={phone} 
                        onChange={setPhone}
                        style={{'fontFamily': 'Audiowide'}}
                    />
                    
                    <label className="input-title" htmlFor="password">Password</label>
                    <input className="input-box" type="password" value={password1} onChange={(e) => setPass1(e.target.value)}/>
                    
                    <label className="input-title" htmlFor="confirm-pass">Confirm Password</label>
                    <input className="input-box" type="password" value={password2} onChange={(e) => setPass2(e.target.value)}/>

                    {joinTeamStatus ? (
                        [
                            <label className="input-title" htmlFor="team">Team Code</label>,
                            <input className="input-box" value={team_code} onChange={(e) => setTeam(e.target.value)}/>
                        ]
                    ) : (
                       <></>
                    )}
                    <div className = "button-container">
                        <EndzoneButton id = "log-in-btn" style={{"margin": "2vh"}} text = "Create Account" onClick = {handleSubmit} icon = {<AddIcon sx = {{"font-size": '150%'}} ></AddIcon>}></EndzoneButton>
                    </div>
                    <p id = "userMessage"></p>
                </form>
            </div> 
        </div>
    );
}

export default CreateAccount;
